<template>
  <div class="auto select-overspread">
    <div  v-if="data.back_reason"  class="panel panel-info">
      <span style="color: red">    请注意被退回原因为:{{data.back_reason}}</span>
    </div>
    <validator name="v">
      <form class="form-horizontal">
        <div class="col-ms-12 form-group">
          <div v-for="(index,item) in data.fields">
            <!--input-->
            <div v-if="item.type==='input'" v-show="!item.hidden" :class="[data.fields[index].required&&!(data.fields[index].value) ? 'has-error' : '',data.fields[index].bootstraped?data.fields[index].bootstraped+' form-group':'col-sm-4 form-group']">
              <label :class="item.label_bootstraped?item.label_bootstraped+' control-label':'control-label col-sm-6'">{{item.label}}：</label>
              <div  :class="item.value_bootstraped?item.value_bootstraped:'col-sm-6'">
                <input class="form-control"
                       :type="item.type"
                       v-model="data.fields[index].value"
                       :placeholder="item.placeholder"
                       :value="data.fields[index].value"
                       :readonly="item.readonly"
                       :disable="item.disable"
                       @change="disabled_check(data.fields[index].required,data.fields[index].value)"
                       @blur ="check_disable"
                />
              </div>
            </div>
            <!--时间datepi
            cker-->
            <div v-if="item.type==='datepicker'" :class="[data.fields[index].required&&!(data.fields[index].value) ? 'has-error' : '',data.fields[index].bootstraped?data.fields[index].bootstraped+' form-group':'col-xs-12 form-group']">
              <label  style="margin-top: 0.7em"  :class="item.label_bootstraped?item.label_bootstraped + '':'col-xs-12'">{{item.label}}：</label>
              <div  :class="item.value_bootstraped?item.value_bootstraped:'col-xs-12'">
                <datepicker
                  :placeholder="item.placeholder"
                  :value.sync="data.fields[index].value"
                  :format="'yyyy-MM-dd HH:mm:ss'"
                  v-model="data.fields[index].value"
                  :readonly="item.readonly"
                  :disabled="item.disabled"
                  @blur ="check_disable"
                  :show-reset-button="reset">
                </datepicker>
              </div>
            </div>


            <!--textarea-->
            <div v-if="item.type==='textarea'"  :class="[data.fields[index].required&&!(data.fields[index].value) ? 'has-error' : '',data.fields[index].bootstraped?data.fields[index].bootstraped+' form-group':'col-sm-12 form-group']">
              <label   :class="item.label_bootstraped?item.label_bootstraped+' control-label':'control-label col-sm-2'">{{item.label}}：</label>
              <div  :class="item.value_bootstraped?item.value_bootstraped:'col-sm-10'">
                <textarea
                  :readonly="item.readonly"
                  :disabled="item.disabled"
                  class="form-control"
                  :rows="item.rows"
                  v-model="data.fields[index].value"
                  @change="disabled_check(data.fields[index].required,data.fields[index].value)"
                  @blur ="check_disable"
                >
                </textarea>
              </div>
            </div>

            <!--radio-->
            <div v-if="item.type==='radio'" :class="[data.fields[index].required&&!(data.fields[index].value) ? 'has-error' : '',data.fields[index].bootstraped?data.fields[index].bootstraped+' form-group':'col-sm-12 form-group']">
              <label  v-if="item.label" :class="item.label_bootstraped?item.label_bootstraped+' control-label':'control-label col-sm-2'">{{item.label}}：</label>
              <div class="col-xs-12">
                <div  :class="item.value_bootstraped?item.value_bootstraped:item.items.length%2 == 0?'col-xs-5': 'col-xs-3'" v-for="(index2,row) in item.items">
                  <label style="margin-top: 0.7em" for="row.label" :class="item.items.length%2 == 0?'col-xs-4': 'col-xs-2'">{{row.label}}</label>
                  <input @blur ="check_disable" :class="item.items.length%2 == 0?'col-xs-1': 'col-xs-1'" :readonly="item.readonly" :disabled="item.disabled" style="margin-top: 10px"  type="radio" :name="index"  :id="row.label" v-bind:value="data.fields[index].items[index2].value"  v-model="data.fields[index].value">
                </div>
              </div>
            </div>

            <!--select-->
            <div v-if="item.type==='select'"  :class="[data.fields[index].required&&!(data.fields[index].value) ? 'has-error' : '',data.fields[index].bootstraped?data.fields[index].bootstraped+' form-group':'col-sm-4 form-group']">
              <label  :class="item.label_bootstraped?item.label_bootstraped+' control-label':'control-label col-sm-6'">{{item.label}}：</label>
              <v-select :class="item.value_bootstraped?item.value_boo0000000000000000 tstraped:'col-sm-6'" @blur ="check_disable"  @click="select_change(index),disabled_check(data.fields[index].required,data.fields[index].value)" :readonly="item.readonly" :disable="item.disabled"  :value.sync="data.fields[index].value" v-model="data.fields[index].value"
                            :options='data.fields[index].options' :valueSingle="true"></v-select>

            </div>

            <!--checkbox-->
            <div v-if="item.type==='checkbox'" :class="[data.fields[index].required&&!(data.fields[index].value) ? 'has-error' : '',data.fields[index].bootstraped?data.fields[index].bootstraped+' form-group':'col-sm-12 form-group']">
              <label  v-if="item.label"   :class="item.label_bootstraped?item.label_bootstraped+' control-label':'control-label col-sm-2'">{{item.label}}：</label>
              <div  style="margin-top: 0.7em" :class="item.value_bootstraped?item.value_bootstraped:'control-label col-sm-2'" v-for="(index2,row) in item.items">
                <label  class="font-size form-group col-sm-6">{{row.label}}</label>
                <input  :readonly="item.readonly"  :disabled="item.disabled" type="checkbox" class="col-sm-6" :id="row.label" v-model="data.fields[index].items[index2].value">
              </div>
            </div>


          </div>
          <!-- onetomany -->
          <div class="col-sm-12" v-for="(i,datagrid) in data.onetomany">
            <table v-show="!datagrid.hidden" class="table table-bordered table-striped table-hover">
              <tr style="background-color: #00A3F0">
                <th style="text-align: center;border-color: #00A3F0"><font color="white">序号</font></th>
                <th v-for="labels in datagrid.fields" style="text-align: center;border-color: #00A3F0">
                  <nobr><font color="white">{{labels.label}}</font></nobr>
                </th>
                <th style="text-align: center;border-color: #00A3F0;padding: 0px">
                  <button type="button" @click="$dispatch('add',i)" class="btn btn-warning"
                          v-if="!data.onetomany[i].nopower">
                    +
                  </button>
                </th>
              </tr>
              <tr v-for="(j,row) in model.rows[i]">
                <td style="text-align: center">{{$index+1}}</td>
                <td v-for="key in surBodyData[i]" style="text-align: center">
                  {{row[key]}}
                </td>
                <td style="text-align: center">
                  <a href="#" @click="delete(i,j)" v-if="!data.onetomany[i].nopower">删除</a>
                </td>
              </tr>
            </table>
          </div>
        </div>
        <div class="col-sm-8 form-group">
          <div class="form-group  col-sm-6">
            <label class=" col-sm-4 control-label">分公司：</label>
            <div class="col-sm-5">
              <input class="form-control" type="text" v-model="model.f_filiale"  disabled>
            </div>
          </div>
          <div class="col-sm-6 form-group">
            <label class="control-label col-sm-6">部门：</label>
            <div class="col-sm-5">
              <input type="text" class="form-control" disabled v-model="model.f_parentname"/>
            </div>
          </div>
        </div>
        <div class="col-sm-11 form-group">
          <div class="col-sm-6 form-group">
            <label class="control-label col-sm-4">操作日期：</label>
            <div class="col-sm-5">
              <input type="text" class="form-control" disabled v-model="model.f_operate_date"/>
            </div>
          </div>
          <div class="col-sm-6 form-group">
            <label class="control-label col-sm-6">操作人：</label>
            <div class="col-sm-5">
              <input type="text" class="form-control" disabled v-model="model.f_operator"/>
            </div>
          </div>
        </div>

      </form>
    </validator>
    <div class="from-group" v-if="showButtonFlag" >
      <center>
        <div v-for="(index,button) in data.buttons" :class="[data.buttons.length==4?'col-xs-3':'',data.buttons.length==3?'col-xs-4':'',data.buttons.length==2?'col-xs-6':'']">
          <!-- 按钮组 -->
          <button  :disabled="(button.button_name==='提交'||button.button_name==='下发')&&disable_button"
                   type="button"  @click="clicked(index,button)"  style="border-radius:5px; "
                   :class="[(button.button_name==='提交'||button.button_name==='下发')&&disable_button?'btn btn-default':'btn btn-primary',data.buttons.length==4 ? 'col-xs-2 col-xs-offset-1':'',data.buttons.length==3 ? 'col-xs-2 col-xs-offset-2':'',data.buttons.length==2 ? 'col-xs-4 col-xs-offset-2':'']">
            {{button.button_name}}
          </button>
        </div>
        <div style="display:block; height:50px; width:100%;"></div>
      </center>
    </div>
    <!-- 模态框 -->
    <modal v-if="showmodal" :show.sync="showmodal" v-ref:modal backdrop="false">
      <header slot="modal-header" class="modal-header">
        <button type="button" class="close" @click="closemodal"><span>&times;</span></button>
        <h4 class="modal-title">{{model.button.button_name}}</h4>
      </header>
      <article  slot="modal-body" class="modal-body">
        <div  class="col-sm-12" v-for="(index,button_field) in data.buttons[model.button.button_index].button_fields">
          <label  class="control-label col-sm-3" style="margin-top: 10px">{{button_field.label}}：</label>
          <div  class="col-sm-7">
            <!--select-->
            <input-select v-if="button_field.type=='select'"  :value.sync="data.buttons[model.button.button_index].button_fields[index].value" v-model="data.buttons[model.button.button_index].button_fields[index].value"
                          :options='button_field.options'></input-select>
            <!-- input -->
            <input v-else type="text" class="form-control" v-model="data.buttons[model.button.button_index].button_fields[index].value"/>
          </div>
        </div>
      </article>
      <footer slot="modal-footer" style="border-top:none" class="modal-footer">
        <button v-show="showmodal" type="button" class="btn btn-primary" @click='acknowledge'>确认</button>
      </footer>
    </modal>

    <!-- onetomany新增模态框 -->
    <modal v-if="showadd" :show.sync="showadd" v-ref:modal backdrop="false" :style="show_hidden">
      <header slot="modal-header" class="modal-header">
        <div style="padding-top: 15px;padding-left: 15px;padding-right: 15px;">
          <button type="button" class="close" @click="closemodal(modeltitle)"><span>&times;</span></button>
          <span class="modal-title" style="border-bottom: 1px solid #e5e5e5;"><font size="3">{{modeltitle}}</font></span>
        </div>
      </header>
      <article  slot="modal-body" class="modal-body">
          <div style="padding-top: 15px;border-top: 0px solid #e5e5e5;border-bottom: 0px solid #e5e5e5;">
            <div class="col-sm-12" v-for="(index,showadd_field) in showadd_fields.fields" v-if="refresh_modal">
              <label v-show="!showadd_field.hidden" class="control-label  col-sm-3" style="margin-top: 10px">{{showadd_field.label}}：</label>
              <div class="col-sm-7" v-show="!showadd_field.hidden">
                <!--时间datepicker-->
                <div v-if="showadd_field.type=='datepicker'"
                     :class="[showadd_fields[index].required&&!(showadd_fields.fields[index].value) ? 'has-error' : '']">
                  <div>
                    <datepicker
                      :placeholder="showadd_field.placeholder"
                      :value.sync="showadd_fields.fields[index].value"
                      :format="'yyyy-MM-dd HH:mm:ss'"
                      v-model="showadd_fields.fields[index].value"
                      :readonly=true
                      :disabled="showadd_fields.fields[index].disabled"
                      @blur="check_disable"
                      :show-reset-button="reset">
                    </datepicker>
                  </div>
                </div>
                <!--select-->
                <input-select @change="select_change_modal(index),disabled_check_modal(showadd_fields.fields[index].required,showadd_fields.fields[index].value)" v-if="showadd_field.type=='select'"
                              @blur="check_disable_modal,disabled_check_modal(showadd_fields.fields[index].required,showadd_fields.fields[index].value)"
                              :mouseout="check_disable_modal,disabled_check_modal(showadd_fields.fields[index].required,showadd_fields.fields[index].value)"
                              :value.sync="showadd_fields.fields[index].value"
                              v-model="showadd_fields.fields[index].value"
                              :options='showadd_fields.fields[index].options'>
                </input-select>
                <!-- input -->
                <input :type="showadd_field.type" @change="select_change_modal(index),check_disable_modal,disabled_check_modal(showadd_fields.fields[index].required,showadd_fields.fields[index].value)" v-if="(showadd_field.type=='input'||showadd_field.type=='number')&&!showadd_field.hidden"
                       @blur="check_disable_modal,disabled_check_modal(showadd_fields.fields[index].required,showadd_fields.fields[index].value)"
                       :mouseout="check_disable_modal,disabled_check_modal(showadd_fields.fields[index].required,showadd_fields.fields[index].value)"
                       :readonly="showadd_field.readonly" class="form-control" v-model="showadd_fields.fields[index].value"
                       :value.sync="showadd_fields[index].value"/>
                <span v-if="showadd_fields.fields[index].error&&showadd_fields.fields[index].error.msg&&showadd_fields.fields[index].error.flag" style="color: red">{{showadd_fields.fields[index].error.msg}}</span>
              </div>
            </div>
          </div>
      </article>
      <footer slot="modal-footer" style="border-top:none" class="modal-footer">
          <div style="padding-top: 15px;padding-left: 15px;padding-right: 15px;text-align: right;">
            <button  :class="disable_button_modal?'btn btn-default':'btn btn-primary'" v-show="showadd" :disabled="disable_button_modal" type="button" @click="acknowledge(modeltitle)">
              确认
            </button>
          </div>
      </footer>
    </modal>
  </div>
</template>
<script>

  // Date格式化
  Date.prototype.Format = function (fmt) {
    var o = {
      "M+": this.getMonth() + 1, //月份
      "d+": this.getDate(), //日
      "H+": this.getHours(), //小时
      "m+": this.getMinutes(), //分
      "s+": this.getSeconds(), //秒
      "q+": Math.floor((this.getMonth() + 3) / 3), //季度
      "S": this.getMilliseconds() //毫秒
    };
    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o)
      if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
  }

  import Vue from 'vue'

  export default {
    title: '报建业务通用组件',
    props:['data'],
    data () {
      return {
          refresh: true, // 控制重新渲染
        model:{
            rows: [],
          button:{
            button_name:null,
            button_index:null
          },
          f_filiale:null,
          f_parentname:null,
          f_operate_date:null,
          f_operator:null
        },
        data:null,
        showmodal:false,
        disable_button:true,
        showButtonFlag:true,
        surBodyData: [],
          showadd: false, // onetomany 新增模态框
          showadd_fields: [],  // onetomany 新增模态框的字段配置
          showadd_index: null,
          modeltitle:'',
          refresh_modal:true,
          tempid:null,
          disable_button_modal:true,
          show_hidden:{}
      }
    },
    created(){
      // 初始化数据
      this.showButtonFlag = Vue.prototype.$showFlag
      this.initializtion()
    },
    methods: {
        update() {
            this.refresh = false
            this.$nextTick(() => {
                this.refresh = true
            })
            this.initializtion()
        },
        // 获取组件中的数据
        getmodel(){
            // 组织model.fields数据
            let modeldatas = {}
            for (let i = 0; i < this.data.fields.length; i++) {
                // checkbox特殊处理
                if (this.data.fields[i].type === 'checkbox') {
                    for (let j = 0; j < this.data.fields[i].items.length; j++) {
                        modeldatas[this.data.fields[i].items[j].field] = this.data.fields[i].items[j].value
                    }
                } else {
                    modeldatas[this.data.fields[i].field] = this.data.fields[i].value
                }
            }
            modeldatas.f_process_id = this.data.f_process_id
            modeldatas.f_filiale = this.model.f_filiale
            modeldatas.f_parentname = this.model.f_parentname
            modeldatas.f_operator = this.model.f_operator
            modeldatas.f_operate_date = this.model.f_operate_date
            return modeldatas
        },
        // 修改
        revise(index, index2) {
            this.showadd_fields = []
            let templet = new Object()
            templet = this.data.onetomany[index]
            for (let j = 0; j < templet.fields.length; j++) {
                if (templet.fields[j].type == 'input') {
                    templet[j] = {}
                    templet[j].value = this.model.rows[index][index2][templet.fields[j].field]
                    templet.fields[j].value = this.model.rows[index][index2][templet.fields[j].field]
                } else {
                    templet.fields[j].value = this.model.rows[index][index2][templet.fields[j].field]
                    if (templet.fields[j].type == 'datepicker') {
                        templet.fields[j].value = new Date().Format("yyyy-MM-dd HH:mm:ss")
                    }
                }
            }
            this.tempid = this.model.rows[index][index2].id
            this.modeltitle = '修改'
            this.showadd_fields = templet
            this.showadd_index = index
            this.showadd = true
        },
        delete(i, j) {
            this.$showMessage("您确定要删除这条记录吗？", ['confirm']).then((res) => {
                if (res == 'confirm') {
                    this.$dispatch('onetomanydelete', i, j)
                }
            })
        },
      // 初始化数据
      initializtion(){
        console.log('进入serviceview的initializtion方法')

          this.model.rows = []
          this.surBodyData = []
          console.log('进入serviceview的initializtio==>'+JSON.stringify(this.data.onetomany))
          if (this.data.onetomany && this.data.onetomany.length > 0) {
              for (let i = 0; i < this.data.onetomany.length; i++) {
                  let temp = []
                  for (let j = 0; j < this.data.onetomany[i].fields.length; j++) {
                      temp.push(this.data.onetomany[i].fields[j].field)
                  }
                  console.log('pushsurBodyData==>'+JSON.stringify(temp))
                  this.surBodyData.push(temp)
              }
              for (let i = 0; i < this.data.onetomany.length; i++) {
                  if (this.data.onetomany[i].rows) {
                      console.log('push rows==>'+JSON.stringify(this.data.onetomany[i]))
                      console.log('push rows==>'+JSON.stringify(this.data.onetomany[i].rows));
                      this.model.rows.push(this.data.onetomany[i].rows)
                  }
              }

          }
        // console.log('this.service'+this.service);
        // let temp = this.data
        // let temp = JSON.parse(JSON.stringify(this.data))
        // this.data = temp
        // datepicker 没有值给当时值
        if(this.data.fields){
          for(let i=0;i<this.data.fields.length;i++){
            if(this.data.fields[i].type==='datepicker'&&!this.data.fields[i].value){
              this.data.fields[i].value=new Date().Format("yyyy-MM-dd HH:mm:ss")
            }
          }
        }

        if(this.data.filiale){
          this.model.f_filiale = this.data.filiale
        }else{
          this.model.f_filiale = Vue.user.f_parentname
        }
        if(this.data.f_parentname){
          this.model.f_parentname = this.data.f_parentname
        }else{
          this.model.f_parentname = Vue.user.f_parentname
        }
        if(this.data.operate_date){
          this.model.f_operate_date = this.data.operate_date
        }else{
          this.model.f_operate_date = new Date().Format("yyyy-MM-dd HH:mm:ss")
        }
        if(this.data.operator){
          this.model.f_operator = this.data.operator
        }else{
          this.model.f_operator = Vue.user.name
        }
      },
      // 模态框点击确定按钮
      acknowledge(val){
          if (val == '新增') {
              let temp = {}
              console.log("开始训话那边离"+JSON.stringify(this.showadd_fields.fields));
              for (let i = 0; i < this.showadd_fields.fields.length; i++) {
                  if (this.showadd_fields.fields[i].value) {
                      temp[this.showadd_fields.fields[i].field] = this.showadd_fields.fields[i].value
                  } else if(this.showadd_fields&&this.showadd_fields[i]&&this.showadd_fields[i].value){
                      temp[this.showadd_fields.fields[i].field] = this.showadd_fields[i].value
                  }
              }
              console.log("push前："+JSON.stringify(this.model.rows))
              console.log("push前："+JSON.stringify(this.showadd_index))
              console.log("push前："+JSON.stringify(this.model.rows[this.showadd_index]))
              this.model.rows[this.showadd_index].push(temp)
              console.log("开始调用cocnfirm"+JSON.stringify(this.model.rows));
              this.$dispatch('confirm', this.data.onetomany[this.showadd_index].tables, temp, this.showadd_index, this.model.rows[this.showadd_index].length - 1)

              this.closemodal(val)
              this.showadd_fields = null
              this.showadd_index = null

          } else {
              for (let i = 0; i < this.data.buttons[this.model.button.button_index].button_fields.length; i++) {
                  this.model.button.button_fields[this.data.buttons[this.model.button.button_index].button_fields[i].field] = this.data.buttons[this.model.button.button_index].button_fields[i].value
              }
              this.showmodal = false
              this.$dispatch('button', this.model)
          }
      },
        // 检测模态框按钮的disable
        disabled_check_modal(required, value) {
            if (required && value) {
                this.disable_button_modal = false

                let fields = this.showadd_fields.fields
                let flag = false
                for (let i = 0; i < fields.length; i++) {
                    if (fields[i].type !== 'checkbox' && fields[i].required && !fields[i].value) {
                        flag = true
                    }
                }
                this.disable_button_modal = flag
            }
        },
        // 检测模态框按钮的disable,反向检查
        check_disable_modal () {
            let fields = this.showadd_fields.fields
            let flag = false
            for (let i = 0; i < fields.length; i++) {
                if (fields[i].type !== 'checkbox' && fields[i].required && !fields[i].value) {
                    flag = true
                }
            }
            let count = 0
            for (let i = 0; i < fields.length; i++) {
                if (fields[i].type !== 'checkbox' && !fields[i].required) {
                    count++
                }
            }
            if (count == 0) {
                flag = false
            }
            this.disable_button_modal = flag
        },
      // 关闭模态框
      closemodal(val){
          this.show_hidden={display:'block'}
          console.log('jinlaile',this.showadd_fields)
          if(this.showadd_fields&&this.showadd_fields.fields&&this.showadd_fields.fields.length){
              for (let i = 0; i < this.showadd_fields.fields.length; i++) {
                  if (this.showadd_fields[i] && this.showadd_fields[i].value) {
                      this.showadd_fields[i].value = ''
                  }
                  if(this.showadd_fields.fields[i] && this.showadd_fields.fields[i].value){
                      this.showadd_fields.fields[i].value = ''
                  }

                  if(this.showadd_fields.fields[i].label==='安装方式'){
                      console.log('隐藏')
                      this.showadd_fields.fields[i].hidden = true
                  }
                  if(this.showadd_fields.fields[i].label==='面板'){
                      console.log('隐藏')
                      this.showadd_fields.fields[i].hidden = true
                  }
                  if(this.showadd_fields.fields[i].label==='连接方式'){
                      console.log('隐藏')
                      this.showadd_fields.fields[i].hidden = true
                  }
                  if(this.showadd_fields.fields[i].label==='安装位置'){
                      console.log('隐藏')
                      this.showadd_fields.fields[i].hidden = true
                  }
                  if(this.showadd_fields.fields[i].label==='容积'){
                      console.log('隐藏')
                      this.showadd_fields.fields[i].hidden = true
                  }
                  if(this.showadd_fields.fields[i].label==='排烟方式'){
                      console.log('隐藏')
                      this.showadd_fields.fields[i].hidden = true
                  }
              }
          }
          if (val!='新增'||val!='修改'||val!='删除') {
              this.disable_button = false
          }
          this.showmodal = false
          if (val == '新增') {
              this.showadd = false
          }
          this.showadd_fields = null
          this.disable_button_modal=true
          this.modeltitle = null
          this.tempid = null
      },
        // 模态框级联操作预留
        select_change_modal(index) {
            if(this.showadd_fields.fields[index].value){
                this.showadd_fields[index] = {}
                this.showadd_fields[index].value = this.showadd_fields.fields[index].value
            }else if(this.showadd_fields[index]&&this.showadd_fields[index].value){
                this.showadd_fields.fields[index].value = this.showadd_fields[index].value
            }
            this.refresh_modal = false
            this.$nextTick(()=>{
                this.refresh_modal = true
            })
            this.$dispatch('select_cascade_modal', this.showadd_index,index)
            // if(this.showadd_fields.fields[index].label=='购货方纳税人识别号'){
            //   if(this.showadd_fields.fields[index].value.length!=15&&this.showadd_fields.fields[index].value.length!=18&&this.showadd_fields.fields[index].value.length!=20){
            //     this.$showMessage("购货方纳税人识别号必须为15位，18位或者20位")
            //     this.showadd_fields.fields[index].value = ''
            //   }
            // }
        },
        // 模态框级联操作更新
        update_modal(){
            this.refresh_modal = false
            this.$nextTick(()=>{
                this.refresh_modal = true
            })
        },
      // 点击按钮
      clicked(index,button){
        console.log(`点击了按钮index:${index}+button:${JSON.stringify(button)}`);
        // 组织model.fields数据
        let modeldatas = {}
        for(let i=0;i<this.data.fields.length;i++){
          // checkbox特殊处理
          if(this.data.fields[i].type==='checkbox'){
            for(let j=0;j<this.data.fields[i].items.length;j++){
              modeldatas[this.data.fields[i].items[j].field]=this.data.fields[i].items[j].value
            }
          }else if(this.data.fields[i].type === 'select'){
            modeldatas[this.data.fields[i].field]=this.data.fields[i].value[0]
          }else{
            modeldatas[this.data.fields[i].field]=this.data.fields[i].value
          }
        }
        modeldatas.f_process_id = this.data.f_process_id
        modeldatas.f_filiale = this.model.f_filiale
        modeldatas.f_parentname = this.model.f_parentname
        modeldatas.f_operator = this.model.f_operator
        modeldatas.f_operate_date = this.model.f_operate_date
        this.model = null
        this.model = modeldatas
        // 初始化model.button数据
        let buttondatas = {
          button_fields:{}
        }
        buttondatas.button_name=button.button_name
        buttondatas.button_index=index
        if(button.button_fields){
          for(let i=0;i<button.button_fields.length;i++){
            buttondatas.button_fields[button.button_fields[i].field]=button.button_fields[i].value
          }
        }
        this.model.button =buttondatas

        if(button.button_name==='退回'){
          this.showmodal=true
        }else if(button.button_name==='下发'){
          this.showmodal=true
        }else{
          this.$dispatch('button',this.model)
        }
      },
      // 级联操作预留
      select_change(index){
        this.$dispatch('select_cascade',index)
      },
      // 检测按钮的disable
      disabled_check(required,value){
        if(required&&value){
          this.disable_button = false

          let fields = this.data.fields
          let flag = false
          for(let i = 0;i<fields.length;i++){
            if(fields[i].type!=='checkbox'&&fields[i].required&&!fields[i].value){
              flag = true
            }
          }
          this.disable_button = flag
        }
      }
    },
    watch:{
      'data.operate_date'(){
        if(this.data.filiale){
          this.model.f_filiale = this.data.filiale
        }else{
          this.model.f_filiale = Vue.user.f_parentname
        }
        if(this.data.f_parentname){
          this.model.f_parentname = this.data.f_parentname
        }else{
          this.model.f_parentname = Vue.user.f_parentname
        }
        if(this.data.operate_date){
          this.model.f_operate_date = this.data.operate_date
        }else{
          this.model.f_operate_date = new Date().Format("yyyy-MM-dd HH:mm:ss")
        }
        if(this.data.operator){
          this.model.f_operator = this.data.operator
        }else{
          this.model.f_operator = Vue.user.name
        }
      },
      deep:true
    },
    computed: {
      // 输入后按钮检测
      check_disable: function (){
        let fields = this.data.fields
        let flag = false
        for(let i = 0;i<fields.length;i++){
          if(fields[i].type!=='checkbox'&&fields[i].required&&!fields[i].value){
            flag = true
          }
        }
        this.disable_button = flag
      }
    },
      events: {
        async 'add'(index) {
            this.show_hidden={display:'block'}
            console.log("进入add方法");
            let templet = new Object()
            console.log("onetomany====>"+JSON.stringify(this.data.onetomany))
            templet = this.data.onetomany[index]
            let count = 0
            if(templet&&templet.fields){
                for (let j = 0; j < templet.fields.length; j++) {
                    templet.fields[j].value = ''
                    if (templet.fields[j].type == 'datepicker') {
                        templet.fields[j].value = new Date().Format("yyyy-MM-dd HH:mm:ss")
                    }
                    // 生产日期防止操作员未选择
                    if (templet.fields[j].type === 'datepicker' && templet.fields[j].label === '生产日期') {
                        templet.fields[j].value = '';
                    }
                    if(templet.fields[j].required){
                        count++
                    }
                }
            }
            if(count==0){
                this.disable_button_modal = false
            }
            console.log("templet====>"+JSON.stringify(templet))
            this.modeltitle = '新增'
            this.showadd_fields = templet
            this.showadd_index = index
            this.showadd = true
            console.log("退出add方法==>"+this.showadd)
        }
    }
  }
</script>



